function CodePanel({
  className = '',
  code,
  line,
  listType,
  typeOfIsert,
  delay = 1000,
}) {
  const [currentRunningIndex, setCurrentRunningIndex] = useState([]);
  const [codeToRun, setCodeToRun] = useState([]);

  useEffect(() => {
    setCurrentRunningIndex(line || []);
    console.log('line', line);
  }, [line]);

  useEffect(() => {
    setCodeToRun(code || []);
  }, [code]);

  return (
    <div className={`${className} ${styles.codePanel}`}>
      <div className={`${styles.container} ${styles.blueBorder}`}>
        <h5>Código sendo Executado</h5>
        <pre className={styles.codebox}>
          {codeToRun.map((item, index) => {
            return (
              <p
                style={{
                  width: '100%',
                  backgroundColor:
                    currentRunningIndex === index ? 'yellow' : '',
                }}
                key={`line-${index}`}
              >
                {item}
              </p>
            );
          })}
        </pre>
      </div>
    </div>
  );
}

export default CodePanel;
